.header {
    margin-top: 16px;

}

.header__nav {
    position: absolute;
    z-index: 1;
    display: flex;
    margin-top: 24px;
}



.header-nav__list {
    padding-top: 8px;
    padding-bottom: 8px;
    align-items: center;
    display: flex;
    margin-left: 398px;
    border-radius: 60px;
    background: rgb(126, 132, 127);
}

.header__logo {
    margin-left: 48px;
    color: rgb(27, 27, 27);
    font-size: 28px;
    font-weight: 700;
    line-height: 28px;
    letter-spacing: -2%;
    text-align: left;
}

.header-nav__item {
    margin-left: 16px;
}

.header-nav__item:last-child {
    margin-right: 16px;
}

.is-active {

    border-radius: 30px;
    background: rgb(255, 255, 255);
    padding-top: 6px;
    padding-left: 26px;
    padding-bottom: 6px;
    padding-right: 26px;
}


.is-active .header-nav__link {
    color: rgb(27, 27, 27);
}

.header-nav__link {

    color: rgb(246, 246, 246);
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0px;
    text-align: left;
}



.header-contacts__list {
    display: flex;
    margin-left: 350px;
    align-items: center;
}

.header-contacts__item {
    margin-left: 12px;
    box-sizing: border-box;
    border: 1px solid rgba(126, 132, 127, 0.2);
    border-radius: 10px;
}

.header-contacts__link {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 8px;
    fill: rgb(126, 132, 127);
}

.header-contacts__link:hover {
    fill: #5F6560;
}